// Breakpoints
// Small devices
@mixin sm {
  @media (min-width: #{$screen-sm-min}) {
    @content;
  }
}

// Medium devices
@mixin md {
  @media (min-width: #{$screen-md-min}) {
    @content;
  }
}

// Large devices
@mixin lg {
  @media (min-width: #{$screen-lg-min}) {
    @content;
  }
}

// Extra large devices
@mixin xl {
  @media (min-width: #{$screen-xl-min}) {
    @content;
  }
}

// Space horizontal
@mixin space-x($size) {
  &>* {
    margin-left: map-get($spacing-sizes, $size);

    &:first-child {
      margin-left: 0;
    }
  }
}

@mixin space-x-reverse($size) {
  &>* {
    margin-right: map-get($spacing-sizes, $size);

    &:last-child {
      margin-left: 0;
    }
  }
}

// Space vertical

@mixin space-y($size) {
  &>* {
    margin-top: map-get($spacing-sizes, $size);

    &:first-child {
      margin-left: 0;
    }
  }
}