// Change currency form component

.fl-c-change-currency-form {
  padding: 30px;
  max-width: 456px;
  border: 1px solid #f8f8f8;
  border-radius: 20px;

  @include space-y(4);

  &__from-title,
  &__to-title {
    font-size: 1rem;
    font-weight: bold;
  }

  &__filter {
    @include space-x-reverse(2);
  }

  &__filter-item {
    background: #fff;
    cursor: pointer;
    margin-top: 0.5rem;
    font-size: 0.8rem;
    display: inline-block;
    border: 1px solid #bfbfbf;
    padding: 0.25rem 0.3rem;
    border-radius: 4px;

    &--active,
    &:hover {
      background: #dadada;
    }
  }

  &__currency {
    width: 100%;
    margin-top: 10px;
    padding: 10px 20px;
    border: 1px solid #bfbfbf;
    border-radius: 3px;
    display: block;
    background: linear-gradient(304deg, rgb(211 231 230 / 56%) 20%, rgb(173 241 194) 80%);
  }

  &__value {
    width: 100%;
    margin-top: 10px;
    padding: 10px 20px;
    border: 1px solid #bfbfbf;
    border-radius: 3px;
    display: block;
  }
}